$primary : #4253d7;

html, body{
    //border: 10px solid $primary;
    margin:0;
    padding:0;
    font-family: "Open sans", sans-serif;
    color:#333;
    background-color: #f6f6f6;
}

#wrap{
    width:640px;
    margin: 64px auto;
}

#heading{
    margin-bottom:1em;
}

#sketch{
    height:320px;
    overflow:hidden;
}

#p5_loading{
    display: none;
}

#catalog{
    display: block;
    columns:4;
}

ln{
    display: block;
    cursor: pointer;
}

ln:hover{
    text-decoration: underline;
}

.sketch-button{
    border-radius:0.1em;
    font-weight:bold;
}

#gui{
    position: absolute;
    top:2px;
    left:2px;
}
